import { Component, OnInit } from '@angular/core';
import {StudentService} from '../../../Services/Student/student.service';
import {Subscription} from 'rxjs';
import {Student} from '../../../Model/Classes/Student';
import {LocalStorageManager} from '../../../Services/Utility/LocalStorageManager';
import {AuthenticationService} from '../../../Services/Utility/authentication.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-home-smart',
  templateUrl: './home-smart.component.html',
  styleUrls: ['./home-smart.component.css']
})
export class HomeSmartComponent implements OnInit {

  private subscriptions : Subscription[] = [];
  private isLoginOk : boolean = true;
  private isNickNameTaken : boolean = false;
  private isMailTaken : boolean = false;

  constructor(public studentService : StudentService,
              public authenticationService : AuthenticationService,
              public router : Router) { }

  ngOnInit() {
  }

  ngOnDestroy(): void {
    for (let i = this.subscriptions.length - 1; i >= 0; i--) {
      const subscription = this.subscriptions[i];
      subscription && subscription.unsubscribe();
      this.subscriptions.pop();
    }
  }

  closeLoginModal(){
    document.getElementById("closeLogin").click();
  }

  closeRegisterModal(){
    document.getElementById("closeRegister").click();
  }

  authenticate($event : Student) {
    this.subscriptions.push(
      this.studentService.authenticate($event.nickName,$event.password).subscribe(
        answer => {
          if(answer == null) this.isLoginOk = false;
          else {
            this.isLoginOk = true;
            this.authenticationService.studentConnected = answer;
            LocalStorageManager.setToken(this.authenticationService.studentConnected.token);
            this.closeLoginModal();
            this.router.navigate([("/profile")]);
          }
        }
      )
    );
  }

  postStudent($event : Student) {
    this.subscriptions.push(
      this.studentService.post($event.toStudentDTO()).subscribe(
        answer => {
          this.authenticationService.studentConnected = answer
          LocalStorageManager.setToken(this.authenticationService.studentConnected.token);
          this.closeRegisterModal();
          this.router.navigate([("/profile")]);
        }
      )
    );
  }

  verifyNickName($event : Student){
    this.subscriptions.push(
      this.studentService.getByNickName($event.nickName).subscribe(
        answer => {
          this.isNickNameTaken = answer != null;
        }
      )
    );
  }

  verifyMail($event : Student){
    this.subscriptions.push(
      this.studentService.getByMail($event.mail).subscribe(
        answer => {
          this.isMailTaken = answer != null;
        }
      )
    );
  }
}
