import {StudentDTO} from '../DTOs/StudentDTO';

export class Student{

  private _id : number;
  private _isArchived : boolean;
  private _isGhostMode : boolean;
  private _nickName : string;
  private _password : string;
  private _mail : string;
  private _token : string;
  private _firstName : string;
  private _lastName : string;
  private _description : string;
  private _birthDate : Date;
  private _diplomaName : string;
  private _school : string;
  private _photo : string;

  constructor() {}

  toStudentDTO():StudentDTO{
    return{
      id: this._id,
      isArchived: this._isArchived,
      isGhostMode: this._isGhostMode,
      nickName: this._nickName,
      password: this._password,
      mail: this._mail,
      firstName: this._firstName,
      lastName: this._lastName,
      description: this._description,
      birthDate: this._birthDate,
      diplomaName: this._diplomaName,
      school: this._school,
      photo: this._photo,
      token: this._token
    };
  }

  fromStudentDTO(studentDTO: StudentDTO):Student{
    Object.assign(this,studentDTO);
    return this;
  }

  get id(): number {
    return this._id;
  }

  set id(value: number) {
    this._id = value;
  }

  get isArchived(): boolean {
    return this._isArchived;
  }

  set isArchived(value: boolean) {
    this._isArchived = value;
  }

  get isGhostMode(): boolean {
    return this._isGhostMode;
  }

  set isGhostMode(value: boolean) {
    this._isGhostMode = value;
  }

  get nickName(): string {
    return this._nickName;
  }

  set nickName(value: string) {
    this._nickName = value;
  }

  get password(): string {
    return this._password;
  }

  set password(value: string) {
    this._password = value;
  }

  get mail(): string {
    return this._mail;
  }

  set mail(value: string) {
    this._mail = value;
  }

  get token(): string {
    return this._token;
  }

  set token(value: string) {
    this._token = value;
  }

  get firstName(): string {
    return this._firstName;
  }

  set firstName(value: string) {
    this._firstName = value;
  }

  get lastName(): string {
    return this._lastName;
  }

  set lastName(value: string) {
    this._lastName = value;
  }

  get description(): string {
    return this._description;
  }

  set description(value: string) {
    this._description = value;
  }

  get birthDate(): Date {
    return this._birthDate;
  }

  set birthDate(value: Date) {
    this._birthDate = value;
  }

  get diplomaName(): string {
    return this._diplomaName;
  }

  set diplomaName(value: string) {
    this._diplomaName = value;
  }

  get school(): string {
    return this._school;
  }

  set school(value: string) {
    this._school = value;
  }

  get photo(): string {
    return this._photo;
  }

  set photo(value: string) {
    this._photo = value;
  }
}
