import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import { faSignOutAlt } from '@fortawesome/free-solid-svg-icons';
import { faSave } from '@fortawesome/free-solid-svg-icons';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { faPowerOff } from '@fortawesome/free-solid-svg-icons';
import {Student} from '../../../Model/Classes/Student';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {LocalStorageManager} from '../../../Services/Utility/LocalStorageManager';
import {Router} from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  faArrowRight = faArrowRight;
  faSave = faSave;
  faSignOutAlt = faSignOutAlt;
  faPowerOff = faPowerOff;

  disabledPasswordChange : boolean = true;
  passwordsMatch : boolean = true;
  newPassword : string;
  confirmPassword : string;

  saveChangesDisabled : boolean = true;

  @Input()
  private isNickNameTaken : boolean;

  @Input()
  private isMailTaken : boolean;

  @Output()
  private eventVerifyNickName : EventEmitter<Student> = new EventEmitter<Student>();

  @Output()
  private eventVerifyMail : EventEmitter<Student> = new EventEmitter<Student>();

  @Output()
  private studentModified:EventEmitter<Student> = new EventEmitter<Student>();

  constructor(public fb : FormBuilder,
              public router : Router) { }

  ngOnInit() {}

  /***** Active student *****/

  private _student : Student;

  get student(): Student {
    return this._student;
  }

  @Input()
  set student(value: Student) {
    this._student = value;
  }

  /***** Methods *****/

  enableChanges(){
    this.saveChangesDisabled = false;
  }

  updatePasswordMatch(){
    this.passwordsMatch = this.newPassword == this.confirmPassword;
  }

  signOut() {
    LocalStorageManager.resetToken();
    this.router.navigate(['home']);
  }

  togglePasswordChange(){
    this.disabledPasswordChange = !this.disabledPasswordChange;
    this.newPassword = "";
    this.confirmPassword = "";
    this.passwordsMatch = true;
  }

  notifyModify(){
    if(!this.disabledPasswordChange){
      this.student.password = this.newPassword;
    }
    this.studentModified.next(this.student);
    this.saveChangesDisabled = true;
    this.disabledPasswordChange = true;
  }

  notifyDisableAccount(){
    this.student.isArchived = true;
    this.studentModified.next(this.student);
    this.saveChangesDisabled = true;
    this.disabledPasswordChange = true;
  }

  notifyEnableAccount(){
    this.student.isArchived = false;
    this.studentModified.next(this.student);
    this.saveChangesDisabled = true;
    this.disabledPasswordChange = true;
  }

  notifyVerifyNickName(){
    let isNotEmpty : boolean = this.student.nickName != "";
    if(isNotEmpty){
      this.eventVerifyNickName.next(this.student);
    }
  }

  notifyVerifyMail(){
    let isNotEmpty : boolean = this.student.mail != "";
    if(isNotEmpty){
      this.eventVerifyMail.next(this.student);
    }
  }

  disabledSaveChanges(){
    // TODO Check on REGEX + Check password
    let nickNameFailed : boolean = this.student.nickName.length < 3
      || this.student.nickName.length > 20
      || this.isNickNameTaken;

    let mailFailed : boolean = this.student.mail.length < 4
      || this.student.mail.length > 50
      || this.isMailTaken;

    return nickNameFailed || mailFailed || !this.passwordsMatch || this.saveChangesDisabled;
  }
}
