import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { PageNotFoundComponent } from './UI/Errors/PageNotFound/page-not-found/page-not-found.component';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { HomeComponent } from './UI/Home/home/home.component';
import { HomeSmartComponent } from './UI/Home/home-smart/home-smart.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { ProfileComponent } from './UI/Profile/profile/profile.component';
import { ProfileSmartComponent } from './UI/Profile/profile-smart/profile-smart.component';
import {ConnectedGuardService} from './Services/Utility/connected-guard.service';
import { DashboardComponent } from './UI/Dashboard/dashboard/dashboard.component';
import { DashboardSmartComponent } from './UI/Dashboard/dashboard-smart/dashboard-smart.component';


const routes: Routes = [
  {
    path: 'home',
    component: HomeSmartComponent
  },
  {
    path: 'profile',
    canActivate:[ConnectedGuardService],
    component: ProfileSmartComponent
  },
  {
    path: 'dashboard',
    canActivate:[ConnectedGuardService],
    component: DashboardSmartComponent
  },
  // Other routes
  {
    path: 'not-found',
    component: PageNotFoundComponent
  },
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: '**',
    redirectTo: '/not-found'
  }
];


@NgModule({
  declarations: [
    AppComponent,
    PageNotFoundComponent,
    HomeComponent,
    HomeSmartComponent,
    ProfileComponent,
    ProfileSmartComponent,
    DashboardComponent,
    DashboardSmartComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    RouterModule.forRoot(routes),
    FontAwesomeModule,
    ReactiveFormsModule,
    FormsModule
  ],
  providers: [ConnectedGuardService],
  bootstrap: [AppComponent]
})
export class AppModule { }
