import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import { faTrophy } from '@fortawesome/free-solid-svg-icons';
import { faUserFriends } from '@fortawesome/free-solid-svg-icons';
import { faTasks } from '@fortawesome/free-solid-svg-icons';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Student} from '../../../Model/Classes/Student';
import {LocalStorageManager} from '../../../Services/Utility/LocalStorageManager';
import {StudentService} from '../../../Services/Student/student.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  readonly ROW_TITLE_CLASS : string = "row justify-content-center p-1 pt-3 pb-3";
  readonly COL_CARDS_CLASS : string = "col-12 col-sm-4 text-center p-1";
  readonly CARDS_CLASS : string = "card bg-dark mx-auto shadow";
  readonly CARDS_HEADERS_CLASS : string = "card-header h5";
  readonly CARD_IMG_CLASS : string = "card-img-bottom";
  readonly ICON_SIZE_CLASS : string = "fa-5x";

  faTrophy = faTrophy;
  faUserFriends = faUserFriends;
  faTasks = faTasks;

  @Output()
  private eventPostStudent : EventEmitter<Student> = new EventEmitter<Student>();

  @Output()
  private eventAuthenticate : EventEmitter<Student> = new EventEmitter<Student>();

  @Output()
  private eventVerifyNickName : EventEmitter<Student> = new EventEmitter<Student>();

  @Output()
  private eventVerifyMail : EventEmitter<Student> = new EventEmitter<Student>();

  @Input()
  private isLoginOk : boolean;

  @Input()
  private isNickNameTaken : boolean;

  @Input()
  private isMailTaken : boolean;

  constructor(public fb : FormBuilder,
              public studentService : StudentService) { }

  ngOnInit() {
  }

  isUserConnected() : boolean{
    return LocalStorageManager.getToken() != null;
  }

  /* Modal navigation */
  step:number = 0;

  next(){
    this.step++;
  }

  back(){
    this.step--;
  }

  forgotPassword(){
    this.step = 3;
  }

  home(){
    this.step = 0;
  }

  /* 0.Login */
  login: FormGroup = this.fb.group({
    nickname : this.fb.control("", Validators.required),
    password : this.fb.control("", Validators.required)
  });

  resetLoginForm(){
    this.login.reset();
    this.isLoginOk = true;
  }

  printLogs(){
    if(this.login.valid){
      console.log(this.login.get('nickname').value);
      console.log(this.login.get('password').value);
    }
  }

  buildAuthenticate() : Student {
    let student : Student = new Student();
    student.nickName = this.login.get('nickname').value;
    student.password = this.login.get('password').value;
    return student;
  }

  notifyAuthenticate() {
    this.eventAuthenticate.next(this.buildAuthenticate());
  }

  /* 1/2.Register */
  registerOne: FormGroup = this.fb.group({
    nickname: this.fb.control("", Validators.required),
    birthdate: this.fb.control("", Validators.required),
  });

  registerTwo: FormGroup = this.fb.group({
    mail : this.fb.control("", Validators.required),
    password : this.fb.control("", Validators.required)
  });

  resetRegisterOneForm(){
    this.registerOne.reset();
    this.isNickNameTaken = false;
  }

  resetRegisterTwoForm(){
    this.registerTwo.reset();
    this.isMailTaken = false;
  }

  printRegister(){
    console.log(this.registerOne.get('nickname').value);
    console.log(this.registerTwo.get('mail').value);
    console.log(this.registerTwo.get('password').value);
  }

  buildStudent() : Student {
    let student : Student = new Student();
    student.isArchived = false;
    student.isGhostMode = false;
    student.nickName = this.registerOne.get('nickname').value;
    student.birthDate = this.registerOne.get('birthdate').value;
    student.mail = this.registerTwo.get('mail').value;
    student.password = this.registerTwo.get('password').value;
    return student;
  }

  buildForNickNameCheck() : Student{
    let student : Student = new Student();
    student.nickName = this.registerOne.get('nickname').value;
    return student;
  }

  buildForMailCheck() : Student{
    let student : Student = new Student();
    student.mail = this.registerTwo.get('mail').value;
    return student;
  }

  notifyPostStudent() {
    this.eventPostStudent.next(this.buildStudent());
  }

  notifyVerifyNickName(){
    let isNotEmpty : boolean = this.buildForNickNameCheck().nickName != "";
    if(isNotEmpty){
      this.eventVerifyNickName.next(this.buildForNickNameCheck());
    }
  }

  notifyVerifyMail(){
    let isNotEmpty : boolean = this.buildForMailCheck().mail != "";
    if(isNotEmpty){
      this.eventVerifyMail.next(this.buildForMailCheck());
    }
  }
}
