import { Component, OnInit } from '@angular/core';
import {StudentDTO} from '../../../Model/DTOs/StudentDTO';
import {Subscription} from 'rxjs';
import {StudentService} from '../../../Services/Student/student.service';
import {LocalStorageManager} from '../../../Services/Utility/LocalStorageManager';
import {Student} from '../../../Model/Classes/Student';

@Component({
  selector: 'app-profile-smart',
  templateUrl: './profile-smart.component.html',
  styleUrls: ['./profile-smart.component.css']
})
export class ProfileSmartComponent implements OnInit {

  private subscriptions: Subscription[] = [];
  private student: Student;
  private isNickNameTaken : boolean = false;
  private isMailTaken : boolean = false;

  constructor(public studentService : StudentService) { }

  ngOnInit() {
    this.loadStudent();
  }

  ngOnDestroy(): void {
    for (let i = this.subscriptions.length - 1; i >= 0; i--) {
      const subscription = this.subscriptions[i];
      subscription && subscription.unsubscribe();
      this.subscriptions.pop();
    }
  }

  loadStudent(){
    const sub = this.studentService
      .getByToken(LocalStorageManager.getToken())
      .subscribe(student => {
        this.student = new Student().fromStudentDTO(student)
      });
    this.subscriptions.push(sub);
  }

  modifyStudent($event: Student) {

    const sub = this.studentService.put($event.toStudentDTO()).subscribe();
    this.subscriptions.push(sub);
  }

  verifyNickName($event : Student){
    this.subscriptions.push(
      this.studentService.getByNickName($event.nickName).subscribe(
        answer => {
          this.isNickNameTaken = answer != null ?
            $event.id != answer.id
            : false;
        }
      )
    );
  }

  verifyMail($event : Student){
    this.subscriptions.push(
      this.studentService.getByMail($event.mail).subscribe(
        answer => {
          this.isMailTaken = answer != null ?
            $event.id != answer.id
            : false;
        }
      )
    );
  }

}
