import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { StudentDTO } from '../../Model/DTOs/StudentDTO';
import { Observable } from 'rxjs';
import { Student } from '../../Model/Classes/Student';
import {LocalStorageManager} from '../Utility/LocalStorageManager';

@Injectable({
  providedIn: 'root'
})
export class StudentService {

  private readonly API_URL:string = "/api/students"

  constructor(public httpClient : HttpClient) { }

  authenticate(nickName : string, password : string) : Observable<StudentDTO> {
    return this.httpClient.get<StudentDTO>( `${this.API_URL}/${nickName}&${password}`);
  }

  post(student : StudentDTO) : Observable<StudentDTO> {
    return this.httpClient.post<Student>(this.API_URL, student);
  }

  put(student : StudentDTO) : Observable<StudentDTO> {
    return this.httpClient.put<Student>(this.API_URL, student);
  }

  getByToken(token : String) : Observable<StudentDTO> {
    return this.httpClient.get<Student>(`${this.API_URL}/${token}`);
  }

  getByNickName(nickName : String) : Observable<StudentDTO> {
    return this.httpClient.get<Student>(`${this.API_URL}/nickname/${nickName}`);
  }

  getByMail(mail : String) : Observable<StudentDTO> {
    return this.httpClient.get<Student>(`${this.API_URL}/mail/${mail}`);
  }


}
